import { useState, useEffect } from 'react';
import { Grid, Typography, Box, FormControl, InputAdornment, OutlinedInput } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { toastService } from '../../services/toasterService'
import { useTheme } from '@mui/material/styles';




// assets
import { SearchOutlined } from '@ant-design/icons';

// services
import { leadService } from '../../services/leadService'


import ClearRoundedIcon from '@mui/icons-material/ClearRounded';
import ModeEditOutlineOutlinedIcon from '@mui/icons-material/ModeEditOutlineOutlined';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';

import { useSelector, useDispatch } from 'react-redux';
import { activeItem } from '../../store/reducers/menu';


// ==============================|| DASHBOARD - DEFAULT ||============================== //

const DashboardDefault = () => {
  const [list, setList] = useState([]);
  const navigate = useNavigate();
  const dispatch = useDispatch();



  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const response = await leadService.getBuyerLeads({draft:false});
      setList(response);
    } catch (error) {
      // Handle error
    }
  };

  // search function
  const handleChange = async (arg) => {
    const response = await leadService.getBuyerLeads({search:arg,draft:false});
    setList(response)
  }

  function debounce(cb, delay) {
    let timer;
    return function (...args) {
      clearTimeout(timer)
      timer = setTimeout(() => cb(...args), delay)
    }
  }
  let handle = debounce(handleChange, 500);

  const deleteLead = async (leadId) => {
    const response = await leadService.deleteLead(leadId);
    if (response) {
      toastService.success(response);
      fetchData()
    }
  }

  const updateLead = async (leadId) => {
    dispatch(activeItem({ openItem: ['Supply-Chain'] }));
    navigate(`/supply-chain/${leadId}`)
  }

  return (
    <Grid container rowSpacing={4.5} columnSpacing={2.75}>
      <Grid item xs={12} sx={{ mb: -2.25 }}>
        <Grid container sx={{ mb: 2 }}>
          <Grid item xs={3} sx={{ display: 'flex', alignItems: 'center' }} >
            <Typography variant='h5'>
              Search For Leads
            </Typography>
          </Grid>
          <Grid item xs={9}>
            <Box >
              <FormControl fullWidth >
                <OutlinedInput
                  size="small"
                  id="header-search"
                  startAdornment={
                    <InputAdornment position="start" >
                      <SearchOutlined />
                    </InputAdornment>
                  }
                  onChange={(e) => handle(e.target.value)}
                  aria-describedby="header-search-text"
                  inputProps={{
                    'aria-label': 'weight'
                  }}
                  placeholder="Ctrl + K"
                  sx={{ backgroundColor: 'rgba(255, 255, 255)' }}
                />
              </FormControl>
            </Box>
          </Grid>
        </Grid>
        {
          list?.map((item) => {
            return (
              <Box sx={{ border: '1px solid #ccc', padding: 2, borderRadius: 2, mb: 2, backgroundColor: 'rgba(255, 255, 255)' }}>
                <Grid container sx={{ pt: 0 }}>
                  <Grid item xs={12} spacing={0.5} container justifyContent="flex-end" alignItems="center" sx={{ height: '0.5rem' }}>
                    <Tooltip title="Update">
                      <IconButton onClick={() => updateLead(item._id)}>
                        <ModeEditOutlineOutlinedIcon style={{ color: 'gray' }} sx={{ fontSize: '1.2rem', p: 0 }} />
                      </IconButton>
                    </Tooltip>
                    <Tooltip title="Delete">
                      <IconButton onClick={() => deleteLead(item._id)}>
                        <ClearRoundedIcon style={{ color: 'gray' }} sx={{ fontSize: '1.2rem', p: 0 }} />
                      </IconButton>
                    </Tooltip>
                  </Grid>
                  {/* <Grid item xs={4} sm={2} sx={{ p: 0, h: 100 }}>
                    <Box sx={{}}>
                      <Typography>
                        Product:
                      </Typography>
                      <Typography>
                        Quantity:
                      </Typography>
                      <Typography>
                        HSN Number:
                      </Typography>
                      <Typography >
                        Specification:
                      </Typography>
                      <Typography>
                        Note: {item.note}
                      </Typography>
                    </Box>
                  </Grid>
                  <Grid item xs={8} sm={10} sx={{ p: 0, h: 100, width: "5px" }}>
                    <Box sx={{}}>
                      <Typography>
                        {item.product}
                      </Typography>
                      <Typography>
                        {item.quantity}
                      </Typography>
                      <Typography>
                        {item?.hsnNumber}
                      </Typography>
                      <Typography variant="body1">
                        {item.specification}
                      </Typography>
                    </Box>
                  </Grid> */}
                  <Grid item xs={4} sm={2} sx={{ py: 0, my: 0 }}>
                    <Typography>
                      Product:
                    </Typography>
                  </Grid>
                  <Grid item xs={8} sm={10} sx={{ py: 0, my: 0, width: "5px" }}>
                    <Typography>
                      {item.product}
                    </Typography>
                  </Grid>
                  <Grid item xs={4} sm={2} sx={{ p: 0, h: 100 }}>
                    <Typography>
                      Quantity:
                    </Typography>
                  </Grid>
                  <Grid item xs={8} sm={10} sx={{ p: 0, h: 100, width: "5px" }}>
                    <Typography>
                      {item.quantity}
                    </Typography>
                  </Grid>
                  <Grid item xs={4} sm={2} sx={{ p: 0, h: 100 }}>
                    <Typography>
                      HSN Number:
                    </Typography>
                  </Grid>
                  <Grid item xs={8} sm={10} sx={{ p: 0, h: 100, width: "5px" }}>
                    <Typography>
                      {item?.hsnNumber}
                    </Typography>
                  </Grid>
                  <Grid item xs={4} sm={2} sx={{ p: 0, h: 100 }}>
                    <Typography >
                      Specification:
                    </Typography>
                  </Grid>
                  <Grid item xs={8} sm={10} sx={{ p: 0, h: 100, width: "5px" }}>
                    <Typography variant="body1">
                      {item.specification}
                    </Typography>
                  </Grid>
                  <Grid item xs={4} sm={2} sx={{ p: 0, h: 100 }}>
                    <Typography>
                      Note:
                    </Typography>
                  </Grid>
                  <Grid item xs={8} sm={10} sx={{ p: 0, h: 100, width: "5px" }}>
                      <Typography variant="body1" sx={{textWrap:'wrap'}}>
                        {item.note}
                      </Typography>
                  </Grid>
                  {/* <Grid item sm={4} sx={{ p: 0, h: 100 }}>
                    <Box sx={{ py: 3, width: '100%', height: "100%" }}>
                      <Typography>
                        buyer : {item.buyer?.name}
                      </Typography>
                      <Typography>
                        email : {item.buyer?.email}
                      </Typography>
                      <Typography>
                        phone : {item.buyer?.phone}
                      </Typography>
                    </Box>
                  </Grid> */}
                </Grid>
              </Box>)
          }
          )
        }
      </Grid>
    </Grid>
  );
};



export default DashboardDefault;